<template>
  <div class="main">
    <div class="nav_main">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span>
          <div class="demo-input-suffix">
        <el-input size="mini" placeholder="" prefix-icon="el-icon-search" v-model="input1"></el-input>
          </div>
      </span>
    </div>
  </div>
  <!---->
</template>

<script>
export default {
  data() {
    return {
      input1: ""
    };
  }
};
</script>




<style scoped>
.main {
  width: 100%;
  display: flex;
  flex-direction: row;
}
.nav_main {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: 47px;
  background: #181717;
}
.nav_main span {
  flex: 0.1;
  color: #ffffff;
  font-size: 14px;
  line-height: 47px;
  font-weight: bold;
}
.nav_main span:last-child{
  display: flex;
  flex-direction: row;
  align-items: center;
}
</style>



